import Vue from 'vue'
import Router from 'vue-router'
import BasicTable from '@/pages/BasicTable'
import EditableTable from '@/pages/EditableTable'


Vue.use(Router)


export default new Router({
  mode:'hash',
  routes: [
    {
      path: '/',
      name: 'BasicTable',
      component: BasicTable
    }
    ,
    {
      path: '/EditableTable',
      name: 'EditableTable',
      component: EditableTable
    }
  ]
})



