        const s = document.createElement('script');
        s.type = 'text/javascript';
        s.src = 'https://cdn.jsdelivr.net/npm/vue';
        document.body.appendChild(s);
        // 对大盒子的操作
        $(".BasicTableSun").each(function () {
          $(this).children(".BasicTableListTitle").children(".BasicTableListIconfont").children(".slide").click(function () {
            $(this).parent(".BasicTableListIconfont").parent(".BasicTableListTitle").next(".sumBoxBox").slideToggle(500);
          });
          $(this).children(".BasicTableListTitle").children(".BasicTableListIconfont").children(".del").click(function () {
            $(this).parent(".BasicTableListIconfont").parent(".BasicTableListTitle").parent(".BasicTableSun").remove();
          })
        });
        // 对小盒子的操作
        $(".EditTableSumBox").each(function () {
          $(this).children(".litterbox").children(".littleslide").click(function () {
            $(this).parent(".litterbox").next(".addTableBox").slideToggle(500);
          });
          $(this).children(".litterbox").children(".littledel").click(function () {
            $(this).parent(".litterbox").parent(".EditTableSumBox").remove();
          });
        });
        // 删除某行
        $(".editTable").each(function () {
          $(this).children("tbody").children(".editTableTr").children(".editTableTd").children(".editTableDel").click(function () {
            $(this).parent(".editTableTd").parent(".editTableTr").remove();
          })
        });
      },
