          //编辑
          edit(item) {
            console.log(item)
            this.editDetail = {
              ListBrand: item.ListBrand,
              ListProName: item.ListProName,
              ListProPrice: item.ListProPrice,
              ListProSmall: item.ListProSmall,
              ListDiscount: item.ListDiscount,
              id: item.id
            }
            this.editlist = true
            this.editid = item.id

          },
          //确认更新
          update(b) {
            //编辑的话，也是传id去服务端
            //axios.get('url',{ID:id}).then((res) =>{
            //			加载列表
            //})
            let _this= this
            for(let i = 0; i < _this.b.products.length; i++) {
              if(_this.b.products[i].id ==this.editid) {
                _this.b.products[i] = {
                  ListBrand: _this.editDetail.ListBrand,
                  ListProName: _this.editDetail.ListProName,
                  ListProPrice: _this.editDetail.ListProPrice,
                  ListProSmall: _this.editDetail.ListProSmall,
                  ListDiscount: _this.editDetail.ListDiscount,
                  id: this.editid
                }
                this.editlist = false
              }
            }
          }
