<template>
  <div class="BasicTableListBox">
    <div class="BasicTableList" :key="index" v-for="(i,index) in BasicTableListTitles"  :style='i.BasicTableListBorder'>
      <div class="BasicTableListTitle" :style="{background:i.tableBgColor}">
        <span class="iconfont">&#xe703;</span><span class="BasicTableListTitleSpan">{{i.tableTitle}}</span>
        <div class="BasicTableListIconfont">
          <!--<a href="#"><span class="iconfont" @click="show(i,index)">&#xe602;</span></a>选项卡-->
          <span class="iconfont slide">&#xe602;</span>
          <span class="iconfont del">&#xe661;</span>
        </div>
      </div>
      <!--<table class="BasicTableListTable" cellspacing="0" cellpadding="0" v-show="index==num" 选项卡>-->
      <div class="tableBox">
        <table class="BasicTableListTable" cellspacing="0" cellpadding="0">
          <tr :key="index" v-for="(j,index) in BasicTableListCon">
            <td>{{j.ord}}</td>
            <td>{{j.FirstName}}</td>
            <td>{{j.LastName}}</td>
            <td>{{j.Username}}</td>
          </tr>
        </table>
      </div>

    </div>
  </div>
</template>

<script>
  import $ from 'jquery';
  export default {
    name: "BasicTableList",
    mounted(){
      $(".BasicTableList").each(function () {
        $(this).children(".BasicTableListTitle").children(".BasicTableListIconfont").children(".slide").click(function () {
          $(this).parent(".BasicTableListIconfont").parent(".BasicTableListTitle").next(".tableBox").slideToggle(500);
        });
      $(this).children(".BasicTableListTitle").children(".BasicTableListIconfont").children(".del").click(function () {
        $(this).parent(".BasicTableListIconfont").parent(".BasicTableListTitle").parent(".BasicTableList").remove();
      })
    })
    },
    methods:{
      // show:function(i,index){  选项卡
      //   i.isShow=!i.isShow;
      //   this.num=index;
      //   console.log(this.num);
      //   console.log(i);
      //   console.log(index);
      //   console.log(i.isShow);
      // }
    },
    data(){
      return{
        // isShow:true, 选项卡
        // num:0,

        BasicTableListTitles:[
          {"tableTitle":"女装","tableBgColor":"#9d4a9c","BasicTableListBorder":{"border":"1px solid #9d4a9c"}},
          {"tableTitle":"男装","tableBgColor":"#de577b","BasicTableListBorder":{"border":"1px solid #de577b"}},
          {"tableTitle":"饰品","tableBgColor":"#74b749","BasicTableListBorder":{"border":"1px solid #74b749"}},
          {"tableTitle":"鞋靴","tableBgColor":"#ffb400","BasicTableListBorder":{"border":"1px solid #ffb400"}}
        ],
        BasicTableListCon:[
          {"ord":"","FirstName":"品牌","LastName":"商品名","Username":"价格"},
          {"ord":"1","FirstName":"薇姿曼","LastName":"2018冬季新款风衣款双面呢小方领时尚肩扣系腰带","Username":"679"},
          {"ord":"2","FirstName":"沫晗依美","LastName":"女款复合皮毛一体韩版时尚通","Username":"1028"},
          {"ord":"3","FirstName":"苏醒的乐园","LastName":"爆款直降大方甜美苏醒的乐园2018冬季短款","Username":"1029"},
        ]
      }
    }
  }
</script>

<style scoped>
  .tableBox{
    /*overflow: hidden;*/
  }
  .BasicTableListTable tr td:nth-child(1){
    width:8.3%;
  }
  .BasicTableListTable tr td:nth-child(2){
    width:17.8%;
  }
  .BasicTableListTable tr td:nth-child(3){
    width:52.4%;
  }
  .BasicTableListTable tr td:nth-child(4){
    width:15.9%;
  }
</style>



